import React, { useEffect, useState } from 'react';
import { Button, Grid, CardActions, Divider, CardContent } from '@mui/material';
import MaterialTable from 'material-table';
import Icons from 'ui-component/material-icons';
import { useDispatch, useSelector } from 'react-redux';
import { getInventoryLookupData, getLookupData, getLookup2Data, createOrUpdate, getSingleList, nullList } from 'store/actions/index';
import { DataGrid } from '@mui/x-data-grid';
import {
    InputText,
    CheckboxButton,
    Register,
    InputNumber,
    DropDownLookup,
    DropDown,
    InputDate,
    InputTime,
    Growl
} from 'ui-component/globalcomponents';
import { validateLookup } from 'utilities/validateLookup';
import MainCard from 'ui-component/cards/MainCard';
import GlobalHeader from 'ui-component/globalcomponents/GlobalHeader/GlobalHeader';
import ElevationScroll from 'ui-component/ElevationScroll';
import { padding } from '@mui/system';
let FormName = 'Transfers';

const Transfers = () => {
    const [isDisable, setisDisable] = useState(false);
    const [onBlurDisable, setOnBlurDisable] = useState(false);
    const [rowEntryCode, setRowEntryCode] = useState('');
    const [isLocationLookUp, setIsLocationLookUp] = useState(false);
    const [isBoMLookUp, setIsBoMLookUp] = useState(false);
    const [LoadingOnSave, setLoadingOnSave] = useState(false);
    const [selectLookupVal, setSelected] = useState(true);
    const [searchedRows, setSearchRows] = useState([]);
    const [filter, setFilter] = useState(false);
    const [saveFunction, dosaveFunction] = useState(0);
    const [IsEditorView, setIsEditorView] = useState(false);
    const [valueBasic, setValueBasic] = React.useState(new Date());
    const [addItemClicked, setAddItemClicked] = useState(false);

    var lookupSelectRow = false;
    var lookupTxtField;

    const [Header, setHeader] = useState({
        TransNo: '',
        TransType: 'XFR',
        TransDate: new Date(),
        RequireDate: new Date(),
        LocationCode: '',
        Location: '',
        Status: '',
        Remarks: '',
        TransTotalTotal: 0
    });

    const [property, setProperty] = useState({
        registerNotOpen: true,
        viewList: false,
        newList: false,
        editList: false
    });

    const [ItemTable, SetItemTable] = useState({
        columns: [
            {
                title: 'Requisition No',
                field: 'TransNo',
                cellStyle: {
                    width: '15%'
                }
            },
            {
                title: 'Item Code',
                field: 'ItemCode',
                cellStyle: {
                    width: '15%'
                }
            },
            {
                title: 'Item',
                field: 'Item',

                cellStyle: {
                    width: '35%'
                }
            },
            {
                title: 'UOM',
                field: 'UOM',
                cellStyle: {
                    width: '25%'
                },
                render: (rowData) => (
                    <Dropdown
                        optionLabel="UOMCode"
                        disabled={disabledItemTable || rowData.ViewData}
                        value={rowData.UOM}
                        style={{ border: '0px solid' }}
                        options={rowData.IN_ItemUOMs}
                        onChange={(e) => updateTableData(e, rowData, 'UOM')}
                        placeholder="Select a UOM"
                    />
                )
            },

            {
                title: 'Quantity',
                field: 'Qty',
                cellStyle: {
                    width: '20%'
                },
                render: (rowData) => (
                    <NumberFormat
                        InputLabelProps={{ shrink: true }}
                        placeholder="0.00"
                        style={{ border: '0px solid', display: 'block' }}
                        customInput={TextFieldRightAlignedForNumber}
                        disabled={rowData.UOM === '' ? true : false || rowData.ViewData}
                        thousandSeparator
                        decimalScale={2}
                        fixedDecimalScale={2}
                        value={rowData.Qty}
                        onChange={(e) => updateTableData(Number(e.target.value.split(',').join('')), rowData, 'Qty')}
                        fullWidth
                    />
                )
            }
        ],
        rows: []
    });

    const [locationLookupList, setLocationLookupList] = useState({
        columns: [],
        rows: []
    });

    const dispatch = useDispatch();
    const lookupRes = useSelector(({ lookupRes }) => lookupRes);
    const AllActionsRes = useSelector(({ AllActionsRes }) => AllActionsRes);
    const onViewData = AllActionsRes?.singleList?.Message?.Header;

    useEffect(() => {
        if (AllActionsRes.error) {
            dispatch(nullList());
        } else if (AllActionsRes?.singleList) {
            if (AllActionsRes?.singleList.Success === true) {
                setHeader(AllActionsRes?.singleList?.Message.Header);
                SetItemTable({
                    ...ItemTable,
                    rows: AllActionsRes?.singleList?.Message.Detail
                });
                dispatch(nullList());
            } else {
                setOnBlueDisable(false);
            }
        }
    }, [AllActionsRes]);

    const goback = () => {
        setProperty((prevstate) => ({
            ...prevstate,
            registerNotOpen: true,
            msg: ''
        }));

        setHeader({
            TransNo: '',
            TransType: 'XFR',
            TransDate: new Date(),
            RequireDate: new Date(),
            LocationCode: '',
            Location: '',
            Status: '',
            Remarks: '',
            TransTotalTotal: 0
        });

        SetItemTable((Prev) => ({
            ...Prev,
            rows: []
        }));

        setIsEditorView(false);

        setisDisable(false);
        // setOnBlurDisable(false)
    };

    const handleClose = () => {
        setProperty((prevstate) => ({
            ...prevstate,
            registerNotOpen: true,
            msg: ''
        }));

        setHeader({
            TransNo: '',
            TransType: 'XFR',
            TransDate: new Date(),
            RequireDate: new Date(),
            LocationCode: '',
            Location: '',
            Status: '',
            Remarks: '',
            TransTotalTotal: 0
        });

        SetItemTable((Prev) => ({
            ...Prev,
            rows: []
        }));

        setIsEditorView(false);

        setisDisable(false);
        // setOnBlurDisable(false)
    };

    const addData = (data) => {
        setProperty((prevstate) => ({
            ...prevstate,
            registerNotOpen: false,
            viewList: false
        }));
        getLocationLookUpData();
        SetItemTable({
            ...ItemTable,
            rows: []
        });
    };

    const editData = (row) => {
        setIsEditorView(true);
        setHeader(row);
        SetItemTable({
            ...ItemTable,
            rows: lookupRes?.lookupData2?.Message.XFRDetail
        });
        setProperty((prevstate) => ({
            ...prevstate,
            registerNotOpen: false,
            viewList: false
        }));
        getLocationLookUpData();
    };

    const viewData = (row) => {
        setIsEditorView(true);
        setProperty((prevState) => ({
            ...prevState,
            CodeMsg: '',
            registerNotOpen: false,
            viewList: true
        }));

        let payload = `FormName=${'Transfers'}&TransNo=${row.TransNo}`;
        dispatch(getSingleList(payload));
        setHeader({
            ...Header,
            RoutingName: onViewData?.RoutingName
        });
        setisDisable(true);
    };

    const SetRowDataID = (data, Button) => {
        if (Button === 'Delete') {
            if (data.TransStatus == '1') {
                setProperty({
                    open: true,
                    severity: 'error',
                    msg: 'This entry cannot b deleted',
                    registerNotOpen: true
                });
                setRowEntryCode('');
            } else {
                setRowEntryCode(data.TransNo);
            }
        }
    };

    const validationOnSave = () => {
        if (Header.ItemCode === '' || Header.ItemCode === undefined || Header.ItemCode === null) {
            setProperty({
                open: true,
                severity: 'error',
                msg: 'Empty BoM Code Not Allowed'
            });
            return false;
        } else if (Header.Quantity === '' || Header.Quantity === undefined || Header.Quantity === null) {
            setProperty({
                open: true,
                severity: 'error',
                msg: 'Empty Qunatity Not Allowed'
            });
            return false;
        } else if (Header.Location === '' || Header.Location === undefined || Header.ShiftHeaLocationd === null) {
            setProperty({
                open: true,
                severity: 'error',
                msg: 'Empty Location Not Allowed'
            });
            return false;
        } else {
            return true;
        }
    };

    const HandleSave = () => {
        if (validationOnSave()) {
            let XFRDetails = Header.XFRStatus === 'Release' ? [...lookupRes?.lookupData2?.Message?.XFRDetail] : [];

            let payload = {
                FormName,
                Header,
                Detail: XFRDetails
            };

            dispatch(createOrUpdate(payload));

            setHeader({
                TransDate: new Date(),
                TransNo: '',
                TransType: 'XFR',
                LocationCode: '',
                Location: '',
                ItemCode: '',
                Item: '',
                UOMCode: '',
                UOM: '',
                RoutingCode: '',
                RoutingName: '',
                TransStatus: '',
                UnitQuantity: '',
                Quantity: '',
                BaseQuantity: '',
                XFRStatus: ''
            });

            setProperty((prevState) => ({
                ...prevState,
                registerNotOpen: true,
                open: false,
                severity: null,
                msg: ''
            }));

            SetItemTable((prev) => ({
                ...prev,
                rows: []
            }));
        }
    };

    const handleSnakeBarClose = () => {
        setProperty({ ...property, open: false });
    };

    const changeLookupVal = (property, value) => {
        lookupTxtField = value;
        lookupSelectRow = false;
        // Header[property] = value;
        // setHeader({ ...Header,  })
    };

    const setLookUpData = (e, rowData, Lookupname) => {
        let value = e.target.value;
        if (Lookupname == 'Location') {
            setHeader({
                ...Header,
                Location: rowData?.Location,
                LocationCode: rowData?.LocationCode
            });
            setIsLocationLookUp(false);
        }
    };

    const getLocationLookUpData = () => {
        dispatch(getInventoryLookupData(`FormName=${'Locations'}`));
    };

    const getTransfersData = () => {
        dispatch(getLookup2Data(`FormName=Transfers&BOMID=${Header.BOMID}&Qty=${Header.BaseQuantity}`));
    };

    const clearAll = () => {
        setHeader({
            ...Header,
            TransNo: '',
            TransType: 'XFR',
            TransDate: new Date(),
            RequireDate: new Date(),
            LocationCode: '',
            Location: '',
            Status: '',
            Remarks: '',
            TransTotalTotal: 0
        });
    };

    useEffect(() => {
        if (lookupRes?.lookupDataInventory) {
            if (lookupRes?.lookupDataInventory.Success === true) {
                setLocationLookupList(lookupRes.lookupDataInventory.Message.Location);
            } else {
                setProperty({
                    open: true,
                    severity: 'error',
                    msg: 'Some Thing Went Wrong'
                });
            }
        }
    }, [lookupRes?.lookupDataInventory]);

    useEffect(() => {
        if (lookupRes?.lookupData2) {
            if (lookupRes?.lookupData2?.Success === true) {
                SetItemTable({
                    ...ItemTable,
                    rows: lookupRes?.lookupData2?.Message.XFRDetail
                });
            } else {
                setProperty({
                    open: true,
                    severity: 'error',
                    msg: 'Some Thing Went Wrong'
                });
            }
        }
    }, [lookupRes?.lookupData2]);

    const handleGetRowId = (e) => {
        return e.CItemCode;
    };

    const AddRow = () => {
        let arr = [...ItemTable.rows];
        let Item = {
            ItemCode: '',
            ItemName: '',
            UOMCode: '',
            CStatus: 0,
            UOM: '',
            UnitQuantity: 1,
            Quantity: 1,
            BaseQuantity: 1,
            StageCode: '',
            StageName: '',
            MachineName: '',
            MachineCode: '',
            StageSeq: ''
            // MachineName: "",
            // MachineCode: "",
            // LaborPerUnit: "",
            // OutputPerUnit: "",
            // PowerPerUnit: "",
        };

        arr.push({ ...Item });
        newRowsArr = arr;
        SetItemTable({ ...ItemTable, rows: arr });
    };

    return (
        <>
            <Grid item xs={12}>
                <Growl property={property} close={handleSnakeBarClose} onClick={handleSnakeBarClose} />
            </Grid>
            {property.registerNotOpen ? (
                <Grid container>
                    <Register
                        Details={'Transfer Entry List'}
                        FormName={FormName}
                        DialogClose={handleClose}
                        dialog={true}
                        AddData={addData}
                        EditData={editData}
                        SetRowDataID={SetRowDataID}
                        dosaveFunction={dosaveFunction}
                        setLoadingOnSave={setLoadingOnSave}
                        ID={rowEntryCode}
                        ViewData={viewData}
                        FormID={FormName}
                        FormCode={'TransNo'}
                        isPostButton={true}
                        isEditButton={true}
                    />
                </Grid>
            ) : (
                <>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <MainCard>
                                <ElevationScroll>
                                    <CardActions>
                                        <Grid container alignItems="center" justifyContent="space-between">
                                            <Grid item>
                                                <GlobalHeader
                                                    goBack={() => goback()}
                                                    clearAll_OnClick={() => clearAll()}
                                                    clearAll={isDisable}
                                                    copyBomDisabled={isDisable}
                                                    saveAndClose={property.viewList}
                                                    saveAndClose_OnClick={() => {
                                                        saveAndClose();
                                                    }}
                                                    FormID={1}
                                                    submitButton={property.viewList}
                                                    SaveButton={property.viewList}
                                                    Button2={!property.newList}
                                                    submit={!property.viewList}
                                                    SaveButton_OnClick={() => HandleSave()}
                                                    submitButton_OnClick={() => postAndSave()}
                                                    Button2_OnClick={() => printVoucher()}
                                                    isView={isDisable}
                                                />
                                            </Grid>
                                        </Grid>
                                    </CardActions>
                                </ElevationScroll>
                                <Divider />
                                <CardContent>
                                    <Grid container spacing={2} alignItems="center">
                                        <Grid item xs={4}>
                                            <InputText label="Trans No" name="TransNo" value={Header.TransNo} disabled={true} />
                                        </Grid>
                                        <Grid item xs={4}>
                                            <InputDate
                                                id="XFRDate"
                                                label="Trans Date *"
                                                name="TransaDate"
                                                value={Header.TransDate}
                                                Change={(date) => setHeader({ ...Header, TransDate: date })}
                                                disabled={isDisable}
                                            />
                                        </Grid>
                                        <Grid item xs={4}>
                                            <DropDown
                                                list={[
                                                    { code: 'XFR', value: 'Direct Transfer' },
                                                    { code: 'IXFR', value: 'Transfer Intransit' }
                                                ]}
                                                value={Header.TransType}
                                                id="XFR"
                                                disabled={isDisable}
                                                Change={(data) => {
                                                    setHeader({ ...Header, TransType: data.target.value });
                                                }}
                                                label="Transfer Type"
                                            />
                                        </Grid>
                                        <Grid item xs={4}>
                                            <DropDownLookup
                                                disabled={isDisable}
                                                label="Source Location *"
                                                name="Location"
                                                reference={'Location'}
                                                onClick={() => setIsLocationLookUp(true)}
                                                lookupOpen={isLocationLookUp}
                                                columns={locationLookupList.columns}
                                                rows={locationLookupList?.rows}
                                                onBlurDiv={() => setIsLocationLookUp(false)}
                                                onFocus={() => setSelected(false)}
                                                onBlur={(e) => {
                                                    setTimeout(() => {
                                                        setFilter(false);
                                                        validateLookup(
                                                            {
                                                                setProperty,
                                                                property,
                                                                Header,
                                                                setHeader,
                                                                selectLookupVal,
                                                                lookupSelectRow
                                                            },
                                                            lookupTxtField,
                                                            locationLookupList.rows,
                                                            ['LocationCode', 'Location'],
                                                            ['Code', 'Description'],
                                                            'Location Code'
                                                        );
                                                        setIsLocationLookUp(false);
                                                    }, 200);
                                                }}
                                                changeLookupVal={changeLookupVal}
                                                propertyName="LocationCode"
                                                listForSearch={locationLookupList?.rows}
                                                setSearchRowProp={(searchResult) => {
                                                    setSearchRows(searchResult);
                                                }}
                                                setFilterProp={(filter) => setFilter(filter)}
                                                setLookupEnable={() => setProperty((prevState) => ({ ...prevState, bomCodeLookUp: true }))}
                                                value={Header.Location}
                                                lookupValuesList={['LocationCode', 'Location']}
                                                setLookUpData={(e, rowData) => setLookUpData(e, rowData, 'Location')}
                                            />
                                        </Grid>
                                        <Grid item xs={4}>
                                            <DropDownLookup
                                                disabled={isDisable}
                                                label="Via Location *"
                                                name="Location"
                                                reference={'Location'}
                                                onClick={() => setIsLocationLookUp(true)}
                                                lookupOpen={isLocationLookUp}
                                                columns={locationLookupList.columns}
                                                rows={locationLookupList?.rows}
                                                onBlurDiv={() => setIsLocationLookUp(false)}
                                                onFocus={() => setSelected(false)}
                                                onBlur={(e) => {
                                                    setTimeout(() => {
                                                        setFilter(false);
                                                        validateLookup(
                                                            {
                                                                setProperty,
                                                                property,
                                                                Header,
                                                                setHeader,
                                                                selectLookupVal,
                                                                lookupSelectRow
                                                            },
                                                            lookupTxtField,
                                                            locationLookupList.rows,
                                                            ['LocationCode', 'Location'],
                                                            ['Code', 'Description'],
                                                            'Location Code'
                                                        );
                                                        setIsLocationLookUp(false);
                                                    }, 200);
                                                }}
                                                changeLookupVal={changeLookupVal}
                                                propertyName="LocationCode"
                                                listForSearch={locationLookupList?.rows}
                                                setSearchRowProp={(searchResult) => {
                                                    setSearchRows(searchResult);
                                                }}
                                                setFilterProp={(filter) => setFilter(filter)}
                                                setLookupEnable={() => setProperty((prevState) => ({ ...prevState, bomCodeLookUp: true }))}
                                                value={Header.Location}
                                                lookupValuesList={['LocationCode', 'Location']}
                                                setLookUpData={(e, rowData) => setLookUpData(e, rowData, 'Location')}
                                            />
                                        </Grid>
                                        <Grid item xs={4}>
                                            <DropDownLookup
                                                disabled={isDisable}
                                                label="Destination Location *"
                                                name="Location"
                                                reference={'Location'}
                                                onClick={() => setIsLocationLookUp(true)}
                                                lookupOpen={isLocationLookUp}
                                                columns={locationLookupList.columns}
                                                rows={locationLookupList?.rows}
                                                onBlurDiv={() => setIsLocationLookUp(false)}
                                                onFocus={() => setSelected(false)}
                                                onBlur={(e) => {
                                                    setTimeout(() => {
                                                        setFilter(false);
                                                        validateLookup(
                                                            {
                                                                setProperty,
                                                                property,
                                                                Header,
                                                                setHeader,
                                                                selectLookupVal,
                                                                lookupSelectRow
                                                            },
                                                            lookupTxtField,
                                                            locationLookupList.rows,
                                                            ['LocationCode', 'Location'],
                                                            ['Code', 'Description'],
                                                            'Location Code'
                                                        );
                                                        setIsLocationLookUp(false);
                                                    }, 200);
                                                }}
                                                changeLookupVal={changeLookupVal}
                                                propertyName="LocationCode"
                                                listForSearch={locationLookupList?.rows}
                                                setSearchRowProp={(searchResult) => {
                                                    setSearchRows(searchResult);
                                                }}
                                                setFilterProp={(filter) => setFilter(filter)}
                                                setLookupEnable={() => setProperty((prevState) => ({ ...prevState, bomCodeLookUp: true }))}
                                                value={Header.Location}
                                                lookupValuesList={['LocationCode', 'Location']}
                                                setLookUpData={(e, rowData) => setLookUpData(e, rowData, 'Location')}
                                            />
                                        </Grid>

                                        <Grid item xs={8}>
                                            <InputText
                                                label="Remarks"
                                                name="Remarks"
                                                value={Header.Remarks}
                                                disabled={isDisable}
                                                Change={(e) => setHeader({ ...Header, Remarks: e })}
                                            />
                                        </Grid>
                                    </Grid>
                                </CardContent>
                            </MainCard>
                            <br />

                            <Grid container spacing={2} alignItems="center">
                                <Grid item xs={12}>
                                    <br />
                                    <MaterialTable
                                        style={{ padding: '15px' }}
                                        title={
                                            <div>
                                                <Button variant="text" onClick={() => AddRow()}>
                                                    âœ” Select Item
                                                </Button>
                                                <Button variant="text" onClick={() => AddRow()}>
                                                    + Add Item
                                                </Button>
                                            </div>
                                        }
                                        columns={ItemTable.columns}
                                        data={ItemTable.rows}
                                        options={{
                                            showTitle: property.viewList === true ? false : true,
                                            paging: false,
                                            actionsColumnIndex: -1,
                                            pageSize: 5,
                                            pageSizeOptions: [5, 10, 20, 30],
                                            search: false,
                                            loadingType: 'linear',
                                            headerStyle: {
                                                fontWeight: 'bold',
                                                paddingLeft: '10px',
                                                paddingRight: '0px',
                                                paddingTop: '0px',
                                                paddingBottom: '0px',
                                                textAlign: 'left'
                                            },
                                            actionsCellStyle: {
                                                justifyContent: 'center',
                                                alignItems: 'center',
                                                width: 20
                                            }
                                        }}
                                        actions={[
                                            (rowData) => ({
                                                icon: () => (
                                                    <DeleteOutline sx={{ color: red[500] }} style={{ height: '1em', width: '1em' }} />
                                                ),
                                                tooltip: 'Delete',
                                                onClick: (event) => {
                                                    onClickDelete(event, rowData);
                                                }
                                            })
                                        ]}
                                        localization={{
                                            body: {
                                                emptyDataSourceMessage: 'No records to display',
                                                filterRow: {
                                                    filterTooltip: 'Filter'
                                                }
                                            }
                                        }}
                                    />
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </>
            )}
        </>
    );
};

export default Transfers;
