
export function validateLookup(stateProps, value, listForSearch, headerValuesList, lookupValuesList, msgProp, callApi, setLookUpData) {

    if (stateProps.lookupSelectRow === false) {
        if (value !== undefined) {
            value = value.toLowerCase()
            if (!stateProps.selectLookupVal) {
                let filteredVal = listForSearch.filter((x, i) => x[lookupValuesList[0]].toString().toLowerCase().indexOf(value) > -1 ||
                    x[lookupValuesList[1]].toString().toLowerCase().indexOf(value) > -1)
                if (filteredVal.length === 0 && value) {
                    stateProps.setProperty((prevState) => ({
                        ...prevState,
                        open: true,
                        severity: 'error',
                        msg: `Invalid ${msgProp}`,
                    }));
                    for (let i = 0; i < headerValuesList.length; i++) {
                        stateProps.Header[headerValuesList[i]] = ""
                        stateProps.setHeader({ ...stateProps.Header })
                    }
                    console.log({ Header: stateProps.Header })
                    // stateProps.setHeader({ ...stateProps.Header })
                }
                else if (filteredVal.length && value) {
                    if (callApi) {
                        switch (msgProp) {
                            case "Customers":
                                setLookUpData(null, filteredVal[0], "Customers")
                                break;
                            case "Item":
                                setLookUpData(null, filteredVal[0], "Item")
                                break;
                            case "Routing":
                                setLookUpData(null, filteredVal[0], "Routing")
                                break;
                            default:
                                setLookUpData("donor", filteredVal[0], headerValuesList, lookupValuesList)
                                break;

                        }



                    }
                    else {
                        for (var i = 0; i < headerValuesList.length; i++) {
                            stateProps.Header[headerValuesList[i]] = filteredVal[0][lookupValuesList[i]]
                            stateProps.setHeader({ ...stateProps.Header })
                        }

                        if (msgProp === 'Bank') {
                            // const dispatch = useDispatch();
                            setLookUpData(filteredVal[0]);
                        }
                    }
                }
            }
        }
        if (value === '') {
            for (let i = 0; i < headerValuesList.length; i++) {
                stateProps.Header[headerValuesList[i]] = "";
                stateProps.setHeader({ ...stateProps.Header })
            }
        }
    }
}

export function validateLookupinJV(stateProps, value, listForSearch, rowData, filterFlag, headerValuesList, lookupValuesList, msgProp) {
    if (stateProps.lookupSelectRow === false) {
        if (value !== undefined) {
            if (!stateProps.selectLookupVal) {
                let filteredVal = listForSearch.filter((x, i) => x[lookupValuesList[0]].toLowerCase().indexOf(value) > -1 || x[lookupValuesList[1]].toLowerCase().indexOf(value) > -1)
                if (filteredVal.length === 0 && value) {
                    stateProps.setProperty({
                        ...stateProps.property,
                        open: true,
                        severity: 'error',
                        msg: `Invalid ${msgProp}`,
                    });
                    for (let i = 0; i < headerValuesList.length; i++) {
                        let index = stateProps.JV.indexOf(rowData);
                        stateProps.JV[index][headerValuesList[i]] = ""
                        stateProps.setJV({ ...stateProps.JV })
                    }
                    stateProps.setJV({ ...stateProps.JV })
                }
                else if (filteredVal.length && value && filterFlag === false) {
                    for (let i = 0; i < headerValuesList.length; i++) {
                        let index = stateProps.JV.indexOf(rowData);
                        stateProps.JV[index][headerValuesList[i]] = filteredVal[0][lookupValuesList[i]]
                        stateProps.setJV({ ...stateProps.JV });
                    }
                }
            }
        }
        if (value === '') {
            for (let i = 0; i < headerValuesList.length; i++) {
                let index = stateProps.JV.indexOf(rowData);
                stateProps.JV[index][headerValuesList[i]] = ""
                stateProps.setJV({ ...stateProps.JV })
            }
        }
    }
}